import React, {useState, useEffect} from 'react';
import {useIntl} from 'react-intl';
import AppsContainer from '../../../@crema/core/AppsContainer';
// import AppComponentCard from '@crema/core/AppComponentCard';
import {AppComponentHeader} from '@crema';
import {TextField} from '@mui/material';
import {Box} from '@mui/system';
import {MobileDateRangePicker} from '@mui/lab';
import CAppContaner from 'components/CappContaner';
import {GET_COST_REPORT} from 'query/orderReoprt/getOrder';
import {useLazyQuery} from '@apollo/client';
import ReportInvoiceRunningCost from 'pages/ecommerce/ReportInvoiceRunningCost';

const runningCostReports = () => {
  const {messages} = useIntl();
  const [value, setValue] = useState([new Date(), new Date()]);
  const [isloding, setIsloding] = useState(true);
  const [getCostRebort, {loading, error, data}] = useLazyQuery(GET_COST_REPORT);
  const handleSubmit = async () => {
    setIsloding(true);

    let firstDate = '';
    let seconDate = '';
    if (value[0] != null) firstDate = dateForm(value[0]);
    if (value[1] != null) seconDate = dateForm(value[1]);
    getCostRebort({
      variables: {startDate: firstDate, endDate: seconDate},
    });
  };
  useEffect(() => {
    console.log(error);
    console.log(loading);
  }, [loading]);

  useEffect(() => {
    if (data) setIsloding(false);
  }, [data]);
  const dateForm = (date) => {
    var dd = String(date.getDate()).padStart(2, '0');
    var mm = String(date.getMonth() + 1).padStart(2, '0'); //January is 0!
    var yyyy = date.getFullYear();
    date = mm + '/' + dd + '/' + yyyy;
    return date;
  };

  return (
    <>
      <AppComponentHeader
        cardStyle={{height: 'auto'}}
        fullView
        title={messages['sidebar.reports.runningCost']}
      />
      <CAppContaner
        title={messages['Datepicker.title']}
        buttonCompMethod={handleSubmit}
        component={() => (
          <>
            <MobileDateRangePicker
              startText={messages['sidebar.reports.startdate']}
              endText={messages['sidebar.reports.enddate']}
              value={value}
              onChange={(newValue) => {
                setValue(newValue);
              }}
              renderInput={(startProps, endProps) => (
                <React.Fragment>
                  <TextField {...startProps} />
                  <Box sx={{mx: 2}}> to </Box>
                  <TextField {...endProps} />
                </React.Fragment>
              )}
              buttonComp={() => {}}
            />
          </>
        )}
      />

      {!isloding ? (
        <>
          {' '}
          <Box sx={{mx: 2}} padding={5}>
            <ReportInvoiceRunningCost data={data.getCostRebort} />{' '}
          </Box>
        </>
      ) : (
        <AppsContainer fullView></AppsContainer>
      )}
    </>
  );
};
export default runningCostReports;
